.page {
    width: 100%;
    height: 100%;
    position: absolute;
}
.top-tags {
    bottom: auto;
    top: 35px;
}
.tags{
    color: #ffd700;
    opacity: 0.8;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 18px;
    font-family: 'La Belle Aurore';
}
.bottom-tag-html {
    margin-left: -20px;
}

.container {
    width: 100%;
    will-change: content;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top:5%;
    margin: 0 auto;
    z-index: 1;
    transform: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
}

.about-page,
.contact-page {
    .text-zone{
        position:absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        width: 35%;
        vertical-align: middle;
        display:table-cell;
        max-height: 90%;

        h1{
            font-size: 53px;
            font-weight: 400;
            font-family: 'Coolvetica';
            color:#ffd700;
            margin-top: 0;
            margin-bottom: 40px;
            left: 10px;
            position: relative;

            &::before{
                content: "<h1>";
                font-family: 'La Belle Aurore';
                position: absolute;
                font-size:18px;
                margin-top:-10px;
                left: -10px;
                opacity: 0.6;
                line-height:18px
            } 
            &::after{
                content: "<h1/>";
                font-family: 'La Belle Aurore';
                position: absolute;
                font-size: 18px;
                line-height: 18px;
                margin-left: 20px;
                left: -30px;
                bottom:-20px;
                opacity: 0.6;
            }
        }
        p {

            color: #fff;
            font-size: 13px;
            font-family: sans-serif;
            letter-spacing: 1px;
            padding-left: 40px;
            max-width: fit-content;
            animation: pulse 1s ease-in;

            &:nth-of-type(1){
                animation-delay: 1.1s;
            }
            &:nth-of-type(2){
                animation-delay: 1.2s;
            }
            &:nth-of-type(3){
                animation-delay: 1.3s;
   
            }
            // &:nth-of-type(1)::before{
            //     content: "<p>";
            //     color: #ffd700;
            // }
        }
    }
}
@media screen and (max-width:1200px){
    .page{
        position:initial;
    }
    .container{
        position: initial;
        height: auto;
        min-height: auto;
    }
}